import { useState, useRef } from 'react';
import classnames from 'classnames';
import { Slider } from 'antd';

const Ambience = (props) => {
  const [enable, setEnable] = useState(false);
  const ref = useRef();
  return (
    <>
      <audio
        ref={ref}
        src={props.src}
        onTimeUpdate={() => {
          var buffer = 1.2;
          if (ref.current.currentTime > ref.current.duration - buffer) {
            ref.current.currentTime = 2;
          }
        }}
        type="audio/x-m4a"
        muted={!enable}
        autoPlay
      />
      <div
        className={`flex items-center pt-8 cursor-pointer select-none ${
          enable ? 'text-secondary' : 'text-grey'
        }`}
        onClick={() => {
          setEnable(!enable);
        }}
      >
        <props.icon className={classnames(props.iconClass)} />
        {props.name}
      </div>
      <Slider disabled={!enable} defaultValue={50}></Slider>
    </>
  );
};

export default Ambience;
